<template>
  <v-container flat>
    <v-toolbar dense flat class="fixed-bar">
      <v-text-field
        hide-details
        prepend-icon="mdi-magnify"
        single-line
        clearable
        v-model="search"
        @input="searchPageDebounced"
      />

      <v-btn icon @click="turnPage(-1)">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>

      <span>{{ currentPage }}</span>

      <v-btn icon @click="turnPage(1)">
        <v-icon>mdi-arrow-right</v-icon>
      </v-btn>
    </v-toolbar>

    <v-img :src="imagePath" contain />
  </v-container>
</template>

<script>
import pagesData from "@/json/data.json";

export default {
  name: "Dictionary",

  data: () => ({
    minPage: 0,
    maxPage: 0,
    currentPage: 0,
    timer: undefined,
    search: null,
    pages: [],
  }),

  computed: {
    fileName() {
      return this.pages[this.currentPage - this.minPage].fileName;
    },
    imagePath() {
      try {
        return require("@/assets/img/dic/" + this.fileName);
      } catch (e) {
        return require("@/assets/img/error404.png");
      }
    },
  },

  methods: {
    turnPage(value) {
      let nextPage = this.currentPage + value;

      if (nextPage < this.minPage || nextPage > this.maxPage) {
        return;
      }
      this.currentPage = nextPage;
    },
    searchPageDebounced() {
      if (!this.search) {
        return;
      }

      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.searchPage();
      }, 500);
    },
    searchPage() {
      const textSearch = this.search.toLowerCase();

      const data = this.pages.find((item) => {
        const locale = "sr-Cyrl";

        return (
          (textSearch.localeCompare(item.first, locale) == 0 ||
            textSearch.localeCompare(item.first, locale) == 1) &&
          (textSearch.localeCompare(item.last, locale) == 0 ||
            textSearch.localeCompare(item.last, locale) == -1)
        );
      });

      if (data) {
        this.currentPage = data.pageNumber;
      }
    },
  },

  created() {
    if (Array.isArray(pagesData) && pagesData.length !== 0) {
      const firstElement = pagesData[0];
      const lastElement = pagesData[pagesData.length - 1];

      this.minPage = +firstElement.pageNumber;
      this.currentPage = +firstElement.pageNumber;
      this.maxPage = +lastElement.pageNumber;

      this.pages = pagesData;
    }
  },
};
</script>

<style scoped>
.fixed-bar {
  position: sticky;
  position: -webkit-sticky; /* for Safari */
  top: 3em;
  z-index: 3;
}
</style>
